import Vue from "vue";
import Router from "vue-router";
import Popular from "./views/Popular.vue";
import Best from "./views/Best.vue";

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "popular",
      component: Popular
    },
    {
      path: "/best",
      name: "best",
      component: Best
    }
  ]
});
