<template>
  <div class="best page">
    <container :titles="titles"></container>
  </div>
</template>

<script>
import Container from "../components/Container";
export default {
  name: "best",
  components: {
    Container
  },
  data() {
    return {
      titles: [
        {
          Title: null,
          Year: null,
          imdbID: null,
          Genre: null,
          Poster: null,
          imdbRating: null,
          id: 1,
          tt: "0111161"
        },
        {
          Title: null,
          Year: null,
          imdbID: null,
          Genre: null,
          Poster: null,
          imdbRating: null,
          id: 2,
          tt: "0068646"
        },
        {
          Title: null,
          Year: null,
          imdbID: null,
          Genre: null,
          Poster: null,
          imdbRating: null,
          id: 3,
          tt: "0071562"
        },
        {
          Title: null,
          Year: null,
          imdbID: null,
          Genre: null,
          Poster: null,
          imdbRating: null,
          id: 4,
          tt: "0468569"
        }
      ]
    };
  },
  created() {
    this.getTitles();
  },
  methods: {
    getTitles() {
      for (let i in this.titles) {
        fetch(
          `http://www.omdbapi.com/?i=tt${this.titles[i].tt}&apikey=746eaed3`
        )
          .then(res => res.json())
          .then(json => {
            this.titles[i].Title = json["Title"];
            this.titles[i].Year = json["Year"];
            this.titles[i].Genre = json["Genre"];
            this.titles[i].Poster = json["Poster"];
            this.titles[i].imdbRating = json["imdbRating"];
            this.titles[i].imdbID = json["imdbID"].slice(2);
          });
      }
    }
  }
};
</script>

