<template>
  <div class="search">
    <input @keyup.enter="search" v-model="input" class="input" type="text" placeholder="Search...">
    <div class="btns">
      <i @click="leaveSearch" v-if="searching" class="cross btn fas fa-times"></i>
      <i @click="search" class="srch btn fas fa-search"></i>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations, mapActions } from "vuex";
export default {
  name: "search",
  data() {
    return {
      input: ""
    };
  },
  computed: mapState(["searching"]),
  methods: {
    ...mapMutations(["leaveSearch"]),
    ...mapActions(["fetchSearch"]),
    search() {
      this.fetchSearch(this.input);
    }
  }
};
</script>

