<template>
  <div class="popular page">
    <container :titles="titles"></container>
  </div>
</template>

<script>
// @ is an alias to /src
import Container from "../components/Container";

export default {
  name: "popular",
  components: {
    Container
  },
  data() {
    return {
      titles: [
        {
          Title: null,
          Year: null,
          Genre: null,
          Poster: null,
          imdbRating: null,
          imdbId: null,
          id: 1,
          tt: "0944947"
        },
        {
          Title: null,
          Year: null,
          Genre: null,
          Poster: null,
          imdbRating: null,
          imdbId: null,
          id: 2,
          tt: "4154796"
        },
        {
          Title: null,
          Year: null,
          Genre: null,
          Poster: null,
          imdbRating: null,
          imdbId: null,
          id: 3,
          tt: "2274648"
        },
        {
          Title: null,
          Year: null,
          Genre: null,
          Poster: null,
          imdbRating: null,
          imdbId: null,
          id: 4,
          tt: "0448115"
        }
      ]
    };
  },

  created() {
    this.getTitles();
  },
  methods: {
    getTitles() {
      for (let i in this.titles) {
        fetch(`http://www.omdbapi.com/?i=tt${this.titles[i].tt}&apikey=746eaed3`)
          .then(res => res.json())
          .then(json => {
            this.titles[i].Title = json["Title"];
            this.titles[i].Year = json["Year"];
            this.titles[i].Genre = json["Genre"];
            this.titles[i].Poster = json["Poster"];
            this.titles[i].imdbRating = json["imdbRating"];
            this.titles[i].imdbID = json["imdbID"].slice(2);
          });
      }
    }
  }
};
</script>

