import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    favorites: [],
    searching: false,
    found: {
      done: false,
      Title: null,
      Year: null,
      Genre: null,
      Poster: null,
      imdbRating: null,
      imdbId: null,
      id: 1,
      tt: null
    }
  },
  mutations: {
    pushToFavs(state, val) {
      if (!state.favorites.includes(val)) {
        state.favorites.push(val);
      }
    },
    popFromFav(state, fav) {
      state.favorites.splice(state.favorites.indexOf(fav), 1);
    },
    enterSearch(state) {
      state.searching = true;
    },
    leaveSearch(state) {
      state.searching = false;
    },
    addFound(state, res) {
      if (res.Response === "False") {
        state.found.done = false;
      } else {
        state.found.done = true;
        state.found.Title = res["Title"];
        state.found.Year = res["Year"];
        state.found.Genre = res["Genre"];
        state.found.Poster = res["Poster"];
        state.found.imdbRating = res["imdbRating"];
        state.found.imdbID = res["imdbID"].slice(2);
        state.found.tt = res["imdbID"].slice(2);
      }
    }
  },
  actions: {
    async fetchSearch({ commit }, s) {
      if (s.length == "") {
        commit("leaveSearch");
      } else {
        let response = await axios.get(
          `http://www.omdbapi.com/?t=${s}&apikey=746eaed3`
        );
        commit("enterSearch");
        commit("addFound", response.data);
      }
    }
  }
});
