<template>
  <div class="favs">
    <p>Favorites</p>
    <div class="underline"></div>
    <div class="favscont">
      <div class="fav" v-for="fav in favorites" :key="fav.id">
        <i @click="remFav(fav)" class="fas fa-times"></i>
        {{fav}}
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "favs",
  computed: mapState(["favorites"]),
  methods: {
    remFav(e) {
      this.$store.commit("popFromFav", e);
    }
  }
};
</script>

