<template>
  <div class="container">
    <div v-if="!searching" class="conts">
      <div
        class="cont"
        v-for="title in titles"
        :key="title.id"
        :style="{ backgroundImage: `url(${title.Poster})` }"
      >
        <div @click="addFav(title.Title)" class="stars">
          <i class="far fa-star regular"></i>
          <i class="fas fa-star solid"></i>
        </div>
        <a class="inner" :href="`https://www.imdb.com/title/tt${title.tt}/?ref_=adv_li_tt`">
          <div class="entry">Title: {{title.Title}}</div>
          <div class="entry">Year: {{title.Year}}</div>
          <div class="entry">Genre: {{title.Genre}}</div>
          <div class="entry">Raiting: {{title.imdbRating}}</div>
          <div class="entry">IMDB id: {{title.imdbID}}</div>
        </a>
      </div>
    </div>
    <div v-else class="found">
      <div v-if="!found.done" class="nothing">Nothing found</div>
      <div v-else class="cont" :style="{ backgroundImage: `url(${found.Poster})` }">
        <div @click="addFav(found.Title)" class="stars">
          <i class="far fa-star regular"></i>
          <i class="fas fa-star solid"></i>
        </div>
        <a class="inner" :href="`https://www.imdb.com/title/tt${found.tt}/?ref_=adv_li_tt`">
          <div class="entry">Title: {{found.Title}}</div>
          <div class="entry">Year: {{found.Year}}</div>
          <div class="entry">Genre: {{found.Genre}}</div>
          <div class="entry">Raiting: {{found.imdbRating}}</div>
          <div class="entry">IMDB id: {{found.imdbID}}</div>
        </a>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "container",
  props: ["titles"],
  data() {
    return {};
  },
  computed: mapState(["searching", "found"]),
  methods: {
    addFav(e) {
      this.$store.commit("pushToFavs", e);
    }
  }
};
</script>

